<style>
  .store-check-wraper {
    border: 1px solid #ececec;
    border-radius: 8px;
    padding: 8px;
    width: 530px;
    box-sizing: border-box;
  }
  
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .del-store-icon-w {
    position: absolute;
    top: -8px;
    right: 2px;
    cursor: pointer;
  }
</style>
<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMsg" label-width="140px">
        <el-form-item label="活动类型">
          <p style="color: #222;font-size: 16px; font-weight: bold;">{{addParamMsg.activityTypeName}}</p>
        </el-form-item>
        <el-form-item label="选择区域">
          <p>{{addParamMsg.regionName}}</p>
        </el-form-item>
        <el-form-item label="活动名称">
          <p>{{addParamMsg.activityName}}</p>
        </el-form-item>
        <el-form-item label="开始时间">
          {{addParamMsg.startDatetime}}
        </el-form-item>
        <el-form-item label="结束时间">
          {{addParamMsg.endDatetime}}
        </el-form-item>
        <el-form-item label="截单时间">
          <div>
            <span>{{addParamMsg.cutoffTypeName}}</span>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <span>{{addParamMsg.cutoffTime}}</span>
          </div>
        </el-form-item>
        <el-form-item label="提货日期">
          <div>截单后第2天</div>
        </el-form-item>
        <el-form-item label="参与门店">
          <div v-if="activityStoresCheckedList.length > 0" class="store-check-wraper">
            <el-table
              class="fixed-height"
              :data="activityStoresCheckedList"
              empty-text="暂无选中任何门店"
              max-height="300"
              size="small">
              <el-table-column min-width="220px" label="门店编码/名称">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="80" prop="cityName" label="城市"></el-table-column>
              <el-table-column min-width="80" prop="saleTypeName" label="门店类型"></el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item label="参与商品">
          <div v-if="checkedList.length > 0">
            <el-table
              class="fixed-height choose-goods-wrap"
              :data="checkedList"
              empty-text="暂无选中任何商品"
              style="width:100%;"
              max-height="560"
              size="small">
              <el-table-column min-width="220px" label="商品信息">
                <template slot-scope="scope">
                  <div class="ll-p-m-w">
                    <div class="ll-img-w">
                      <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                      <img v-else src="/shop/static/img_error.png" alt="">
                    </div>
                    <div class="ll-p-m-c-r">
                      <div>
                        {{scope.row.goodsName || '-'}}
                      </div>
                      <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                      <div class="ll-erp-p-no">商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
                      <div class="ll-erp-p-no">商品编码：{{scope.row.goodsId || '--'}}</div>
                    </div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
              <el-table-column min-width="100px" label="原价(必填)" prop="price"></el-table-column>
              <el-table-column min-width="110px" label="活动价(必填)" prop="preSalePrice"></el-table-column>
              <el-table-column min-width="90px" label="活动库存(选填)" prop="stock"></el-table-column>
              <el-table-column min-width="90px" label="每人限购" prop="limitCount"></el-table-column>
              <el-table-column min-width="90px" :render-header="sortNumHeader" prop="sortNum"></el-table-column>
              <el-table-column min-width="190px" :render-header="sfInStockHeader">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.sfInStock ? '是' : '否'}}
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <p>
              <span style="color:red;">*</span>活动未发布时可以编辑商品，发布后只可添加新商品，不可再编辑已有商品；
            </p>
            <p>
              <span style="color:red;">*</span>活动进行中时新上门店，请在活动商品中发布至门店；
            </p>
          </div>
        </el-form-item>

        <hr class="s-slip-tb">
        <el-form-item>
          <el-button size="small" @click="doCancelHandel">返回</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  const requestUrl = {
    // 详情
    detail: '/plateform/a/preSale/activity/detail'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        activityId: '', // 活动id
        addParamMsg: {
          platformId: 5,
          DIFFCITY: true,
          regionCode: '', // 区域ID
          activityType: 41, // 活动类型
          activityTypeName: '新人专享', // 活动类型名称
          activityName: '', // 活动名称
          startDatetime: '', // 开始时间cycleType
          endDatetime: '', // 结束时间
          shortCycleType: 1, // 周期类型  1 每天 2 每两天
          cutoffTime: '22:00',
          cutoffTypeName: '',
          shortPickUpType: 0, // 提货类型 截单后第2天提
          goodsReqList: [],
          storeNoList: []
        },
        // 参与门店选中的
        activityStoresCheckedList: [],
        // 当前选中的商品用于批量设置价格
        checkedList: []
      }
    },
    created() {
      this.activityId = this.$route.params.activityId
      this.getDetail()
    },
    methods: {
      /**
       * [getDetail 获取拼团活动详情]
       * @return {[type]} [description]
       */
      getDetail() {
        utils.showLoading()
        this.$http.post(requestUrl.detail, {activityId: this.activityId, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              this.addParamMsg.regionCode = res.data.regionCode
              this.addParamMsg.regionName = res.data.regionName
              this.addParamMsg.activityName = res.data.activityName
              if(res.data.activityType) this.addParamMsg.activityType = res.data.activityType || 41
              if(res.data.presaleType) this.addParamMsg.presaleType = res.data.presaleType
              if(res.data.cycleType) this.addParamMsg.cycleType = res.data.cycleType
              this.addParamMsg.startDatetime = res.data.activityStartDate ? res.data.activityStartDate : ''
              this.addParamMsg.endDatetime = res.data.activityEndDate ? res.data.activityEndDate : ""
              this.addParamMsg.shortCycleType = res.data.cutoffDays ? parseInt(res.data.cutoffDays) : 1
              this.addParamMsg.shortPickUpType = 0
              this.addParamMsg.cutoffTime = res.data.cutoffTime
              this.addParamMsg.cutoffTypeName = res.data.cutoffTypeName || ''
              this.activityStoresCheckedList = res.data.storeNoList && res.data.storeNoList.length ? res.data.storeNoList : []
              this.checkedList = res.data.goodsVoList && res.data.goodsVoList.length > 0 ? res.data.goodsVoList : []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      limitGroupTypeFormater(row, column, cellValue) {
        if(cellValue === 0) {
          return "全部"
        }else if(cellValue === 1) {
          return "新会员"
        }else if(cellValue === 2) {
          return "老会员"
        }
      },
      /**
       * [copyUrl 复制链接成功]
       * @return {[type]}      [description]
       */
      onCopySuc() {
        this.$message.success("链接已复制至剪切板，仅限微信公众号中使用")
      },
      sfInStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>1、顺丰标品设为顺丰仓现货时，下单会校验仓库库存；</p>
              <p>2、本来鲜生鲜品无需设置；</p>
            </div>
            <span slot="reference">是否为顺丰仓现货<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * [onCopyError 复制链接失败]
       * @return {[type]} [description]
       */
      onCopyError() {
        this.$message.error("复制链接失败,请手动复制")
      },
      /**
       * [doCancelHandel 返回]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$router.push('/store_act')
      },
      sortNumHeader() {
        return (
          <el-popover
            placement="top-start"
            trigger="hover"
            content="0-299 越大越靠前">
            <span slot="reference">排序权重<i class="el-icon-question"></i></span>
          </el-popover>
        )
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }

</script>



